nav {
    position: fixed;
    z-index: 1;
    top:0px;
    width: 100%;
    margin-bottom: 30px;

    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: gray;
    color: white;
    padding:5px;

}

ul {
    
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 50%;
    list-style: none;
    font-weight: bold;
}

li{
    cursor: pointer;
}

a{
    text-decoration: none;
    color: white;
    
}

.content{
    
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-color: crimson;
    margin: 30px;
    border-radius: 30px;
}

.content-header{
 color: white;
}

main{
    position: relative;
    z-index: 1;
    top:90px
}

/* card.jsx */
.card {
  width: 30%;
  height: 80vh;
  margin: auto;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  background-color: green;
  margin-top: 30px;
  margin-bottom: 30px;
  transition: 1s;

}

.card:hover{

  scale: 1.2
}

