.skillparent{
    height: 1000px;
    padding-top: 20px;
    background-color: rgba(127, 255, 212, 0.733);
}

.skillheading{
    font-size: 50px;
    font-weight: 100;

}

.cardparent{
    width: 70%;
    height: auto;
    margin: auto;
    
    display: grid;
    grid-template-columns: repeat(3,1fr);
    grid-template-rows: 3;
    gap: 30px;
    
    
    
}

.skill{
    border: 2px solid gray;
    width: 150px;
    height: 170px;
    padding-top:15px;
    padding-bottom: 5px;
    text-align: center;
    margin: auto;
    border-radius: 10px;
    transition-duration: .5s;
    background-color: white;
}
.skill > img{
    width: 80%;
    height: 70%;
}

.skill:hover{
    scale: 1.05;
    box-shadow: rgba(0, 0, 0, 0.25) 0px 14px 28px, rgba(0, 0, 0, 0.22) 0px 10px 10px;
}