.projectparent{
    /* border: 1px solid red; */
    padding: 30px;
    background-color: rgba(255, 0, 0, 0.289);
}

.headingproject{
    font-size: 50px;
    font-weight: 100;
}
.pcard{
    display: flex;
    border: 3px solid black;
    width: 65%;
    margin: auto;
    margin-top: 30px;
    margin-bottom: 30px;
    transition-duration: 1s;
}
.pcard:hover{
    scale: 1.1;
}
.pcard > div {
    width: 50%;
}
.pcard > div:nth-child(2)
{
    background-color:darkorange;
    color: white;
}

.pcard > div:nth-child(2) button{
    background-color: black;
    margin: 20px;
}

.banner>img{
  width: 100%;
  height: 100%;
}